@import url(https://fonts.googleapis.com/css?family=Cormorant&display=swap);
@import url(https://fonts.googleapis.com/css?family=EB+Garamond&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cormorant&display=swap);
* {
  text-decoration: none;
}

* p {
  font-family: "Cormorant", serif;
}

.header {
  cursor: pointer;
  border: solid 1px #f2f2f2;
  padding: 15px;
  background-color: #9b9b9bd3;
  color: #fff;
  font-family: "Cormorant", serif;
}
@media (max-width: 790px) {
  .header {
    font-size: 10px;
    padding: 5px;
  }
}

.content {
  cursor: pointer;
  background-color: #fff;
  border-left: solid 1px #f2f2f2;
  border-bottom: solid 1px #f2f2f2;
  padding: 5px;
  margin-right: 5em;
  font-size: 14px;
  width: 100%;
  font-family: "Cormorant", serif;
}
@media (max-width: 790px) {
  .content {
    padding: 5px 0px 0px 0px;
    font-size: 10px;
  }
}

@media (max-width: 790px) {
  .navUL {
    -webkit-padding-start: 20px;
            padding-inline-start: 20px;
  }
}

.navbar {
  position: relative;
}

.navbar #home {
  text-align: center;
}

.navButton {
  width: 50px;
  -webkit-animation: rotation 10s infinite linear;
          animation: rotation 10s infinite linear;
  margin-left: 5vw;
  position: fixed;
  display: flex;
  top: 90px;
  flex-direction: column;
  justify-content: center;
  z-index: 2;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.video {
  margin: 0 auto;
  margin-left: 10%;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}

.videoScreen {
  margin: 0 auto;
  margin-left: 10%;
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}

.contact {
  margin-left: 10%;
}

.contactScreen {
  margin-left: 10%;
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}

.contact a {
  cursor: pointer;
}

@media (max-width: 730px) {
  .videoPlayer {
    width: 100%;
  }
}

@media (max-width: 380px) {
  .videoPlayer {
    width: 200px;
  }
}

.name {
  font-family: "EB Garamond", serif;
  font-size: 8em;
  overflow-wrap: break-word;
  background: -webkit-gradient(linear, left top, left bottom, from(#eee), to(#333));
  background: linear-gradient(#eee, #333);
  background-clip: text;
  transition: all 0.6s ease;
  background: -webkit-linear-gradient(#eee, #333);
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
  -webkit-transition: all 0.6s ease;
}

.nameAfter {
  font-family: "EB Garamond", serif;
  font-size: 8em;
  overflow-wrap: break-word;
  background: -webkit-linear-gradient(#eee, rgb(41, 170, 255));
  background: -webkit-gradient(linear, left top, left bottom, from(#eee), to(rgb(41, 170, 255)));
  background: linear-gradient(#eee, rgb(41, 170, 255));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
  transition: all 0.6s ease;
  -webkit-transition: all 0.6s ease;
}

@media (max-width: 600px) {
  .name .nameAfter {
    font-size: 3em;
  }
}

.nameTag {
  font-family: "Cormorant", serif;
  position: absolute;
  z-index: -1;
}

#tagBefore {
  color: #000;
  transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
}

#tagAfter {
  color: rgb(167, 167, 167);
  transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
}

.em {
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}

.emScreen {
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}

.em-container {
  display: inline-flex;
  flex-direction: row;
}
@media (max-width: 1000px) {
  .em-container {
    flex-direction: column;
  }
}

.em-image1 img {
  max-width: 700px;
}
@media (max-width: 1000px) {
  .em-image1 img {
    max-width: 550px;
  }
}
@media (max-width: 425px) {
  .em-image1 img {
    max-width: 300px;
  }
}

.em-text {
  font-family: "Cormorant", serif;
  text-align: left;
  margin-top: 10%;
  padding-left: 1em;
  padding-right: 1em;
}
@media (max-width: 1000px) {
  .em-text {
    margin-top: 0;
    padding-left: 0;
  }
}

.butw {
  margin-left: 6em;
  display: flex;
  flex-direction: row;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 790px) {
  .butw {
    flex-direction: column-reverse;
  }
}

.butwScreen {
  margin-left: 6em;
  display: flex;
  flex-direction: row;
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 790px) {
  .butwScreen {
    flex-direction: column-reverse;
  }
}

.butw-text {
  font-family: "Cormorant", serif;
  text-align: right;
  width: 30%;
  padding-right: 2em;
}

@media (max-width: 790px) {
  .butw-text {
    width: 100%;
  }
}

.butw-img img {
  width: 100%;
  max-width: 999px;
}

.soundCloud {
  overflow: hidden;
  padding-top: 2em;
}

.blanche-txt {
  font-family: "Cormorant", serif;
  color: #fff;
  position: absolute;
  top: 30%;
}

.blanche-img {
  min-height: 100%;
  min-width: 700px;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  z-index: -1;
}

.monuments {
  display: flex;
  flex-direction: row;
  margin-left: 10%;
  width: auto;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 1200px) {
  .monuments {
    flex-direction: column;
  }
}

.monumentsScreen {
  display: flex;
  flex-direction: row;
  margin-left: 10%;
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 1200px) {
  .monumentsScreen {
    flex-direction: column;
  }
}

.monument-text {
  width: auto;
  top: 0;
  padding: 0 1em 0 2em;
}
@media (max-width: 1200px) and (min-width: 801px) {
  .monument-text {
    padding: 0 1em 0 0em;
  }
}
@media (max-width: 800px) {
  .monument-text {
    width: 95%;
    padding: 0;
  }
}

.welcome {
  display: flex;
  flex-direction: row;
  margin-left: 10%;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 1200px) {
  .welcome {
    flex-direction: column;
    margin-left: 12%;
  }
}

.welcomeScreen {
  display: flex;
  flex-direction: row;
  margin-left: 10%;
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 1200px) {
  .welcomeScreen {
    flex-direction: column;
    margin-left: 12%;
  }
}

.welcome-text {
  position: relative;
  width: 50%;
  top: 0;
  padding: 0 1em 0 2em;
}
@media (max-width: 1200px) {
  .welcome-text {
    max-width: 90vw;
    min-width: 70vw;
    padding: 0 1em 0 0em;
  }
}

#welcomePop {
  font-family: "Cormorant", serif;
  cursor: pointer;
  background: rgba(228, 228, 228, 0.205);
  border: 0.02px solid rgba(46, 46, 46, 0.365);
  padding: 0.2em;
  width: 40%;
  text-align: center;
}

#welcomeHead {
  font-family: "Cormorant", serif;
}

.guide {
  display: flex;
  flex-direction: row;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 600px) {
  .guide {
    flex-direction: column;
    align-items: flex-end;
  }
}

.guideScreen {
  display: flex;
  flex-direction: row;
  opacity: 0.3;
  transition: all 0.1s ease;
  -webkit-transition: all 0.1s ease;
}
@media (max-width: 600px) {
  .guideScreen {
    flex-direction: column;
    align-items: flex-end;
  }
}

.guide-text {
  margin-left: 10%;
}

.imgButton img {
  max-width: 300px;
}

.button-container {
  position: relative;
  width: 300px;
  height: 100%;
}

.imgButton {
  width: 300px;
  height: 100%;
}

.imgButton:hover {
  opacity: 0.5;
}

.guideButton:hover ~ .imgButton {
  opacity: 0.5;
}

.imgFlip {
  background-color: rgb(85, 85, 85);
}

.guideButton button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: #555;
  color: white;
  font-size: 16px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  z-index: 1;
}

